<template>
  <div class="problem">
    <div class="grid">
      <div class="box">
        <p class="title">你是猪么?</p>
        <img src="../assets/pig.png" alt="" class="pig">
        <ul class="list" v-for="(item,index) in showList" :key="index">
          <li v-text="item"></li>
        </ul>
      </div>
      <div class="btn-group">
        <div class="btn" @click="deny()">是</div>
        <div class="btn" @click="confirm()" v-show="isHide">不是</div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: '',
    data() {
      return {
        list: ["怎么？不承认？", "承认了吧，没人笑你", "快承认了吧，这儿没人知道的",
          "哼，那我消失了", "不承认，真消失了", "消失，看你承不承认，哼",'现在没的选了啦，还是承认了吧'
        ],
        showList: [],
        isHide: true
      }
    },
    methods: {
      deny() {
        alert('哈哈哈！！！这才乖，你是猪');
        setTimeout(() => {
          alert('因为喜欢，才有这般心思来捉弄')
        }, 10000);
      },
      confirm() {
        const len = this.showList.length;
        if (len >= 6) {
          this.isHide = false;

          setTimeout(() => {
            alert('因为喜欢，才有这般心思来捉弄')
          }, 10000);
        }

        this.showList.push(this.list[len])
      }
    },
  }
</script>

